.TodoItem{display: flex; align-items: center; gap: 10%; padding: 1.6vw 1vw; border-bottom: 1px solid #d9d9d9;}
.TodoItem input{width: 5%;}
.TodoItem .content{flex: 1;}
.TodoItem .date{font-size: 0.8rem; color: gray;}
.TodoItem button{cursor: pointer; font-size: 0.8rem; border: none; border-radius: 5px; padding: 5px;}
.TodoItem button:hover{background-color: #333; color: #fff;}