.List{}
.List h4{font-size: 1.2rem;}
.List > input{font-size: 1rem; border: none; width: 100%; border-bottom: 1px solid #d9d9d9; padding: 1.4vw; box-shadow: 2px 2px 3px rgba(0,0,0,0.1); border-radius: 5px; box-sizing: border-box;}
.List input[type="text"]:focus{outline: none; border-bottom: 1px solid rgb(37, 147, 255); border-right: 1px solid rgb(37, 147, 255); box-shadow: 2px 2px 3px rgba(37, 147, 255, 0.3);}
.todo_wrapper{display: flex; flex-direction: column; margin-top: 1.8vw;}