.Editor{display: flex; gap: 2vw; width: 100%;}
.Editor > input{flex: 1; padding: 2vw; border: 1px solid rgba(220, 220, 220); border-radius: 5px; font-size: 1rem; outline: none;}
.Editor button{cursor: pointer; width: 14%; border: none; background-color: rgb(37, 147, 255); color: #fff; border-radius: 5px; font-size: 1rem;}
.Editor button:hover{background-color: #052a79;}